









import { Component, Emit, Prop, Vue } from 'vue-property-decorator';

@Component({
  name: 'UIFormSelect',
})
export default class UIFormSelect extends Vue {
  public $el!: HTMLSelectElement;

  @Prop({ type: String })
  public readonly value!: string;

  /** @method */
  @Emit('input')
  public onInput(): string {
    return this.$el.value;
  }
}
